import Vue from 'vue'
import App from './App.vue'
import vuetify from '@/plugins/vuetify'
import VueRouter from 'vue-router'
import Sel from './components/sel_Func.vue'
import ins_Inp from './views/ins_Inp.vue' 
import ins_Confi from './views/ins_Confi.vue'
import ins_Comp from './views/ins_Comp.vue'
import upd_Inp from './update/upd_Inp.vue'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'sel',
        component: Sel
    },
    {
        path: '/ins_inp',
        name: 'ins_Inp',
        component: ins_Inp
    },
    {
        path: '/ins_confi',
        name: 'ins_Confi', 
        component: ins_Confi 
    },
    {
        path: '/ins_comp',
        name: 'ins_Comp', 
        component: ins_Comp 
    },
    {
        path: '/upd_inp',
        name: 'upd_Inp',
        component: upd_Inp
    },
]

const router = new VueRouter({
    routes
})

new Vue({
    render: h => h(App),
    vuetify,
    router
}).$mount('#app')


